import React, { useState } from 'react';
import './player.css';

import roblox from '../assets/roblox.mp3';
import siu from '../assets/siu.mp3';
import ay_mi_madre from '../assets/ay-mi-madre.mp3';
import siu2 from '../assets/siu2.mp3';

export const Player = () => {

    let audios = [];
    audios.push(roblox);
    audios.push(siu);
    audios.push(ay_mi_madre);
    audios.push(siu2);

    let audiosObject = [];
    for (let i = 0; i < audios.length; i++) {
        if (i === 1) {
            let audioTemp = new Audio(audios[i]);
            audioTemp.volume = 0.2;
            audiosObject.push(audioTemp);
        } else {
            audiosObject.push(new Audio(audios[i]));
        }
    }

    const [isPlaying, setIsPlaying] = useState(false);

    const playAudio = (i) => {

        for (let i = 0; i < audiosObject.length; i++) {
            audiosObject[i].currentTime = 0;
        }

        setIsPlaying(true);

        if (isPlaying) {
            for (let i = 0; i < audiosObject.length; i++) {
                audiosObject[i].pause();
            }
        }


        audiosObject[i].play();

    }

    const endedAudio = () => {
        setIsPlaying(false);
        for (let i = 0; i < audiosObject.length; i++) {
            audiosObject[i].currentTime = 0;
        }
    }

    return (
        <>
            <div className="mt-5">
                <h4 className="text-center">Pulsa en los botones y ocurrirÃ¡ la magia âœ¨</h4>
            </div>
            <div className="row justify-content-center mt-3 mb-2 pb-4">
                <div className="col-6 text-center p-3">
                    <button onClick={() => playAudio(0)} onEnded={() => endedAudio()} className="btn btn-primary btn-circle">Oof</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={() => playAudio(1)} onEnded={() => setIsPlaying(false)} className="btn btn-secondary btn-circle">El bicho 1 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={() => playAudio(2)} onEnded={() => setIsPlaying(false)} className="btn btn-success btn-circle">El bicho 2 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={() => playAudio(3)} onEnded={() => setIsPlaying(false)} className="btn btn-danger btn-circle">El bicho 3 ðŸ˜Ž</button>
                </div>
                {/* <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-warning btn-circle">El bicho 5 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-info btn-circle">El bicho 6 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-light btn-circle">El bicho 7 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-dark btn-circle">El bicho 8 ðŸ˜Ž</button>
                </div> */}
            </div>
            <div>
                <p className="text-end"><i>Creado por Christianinski Inc.</i></p>
            </div>
        </>
    )
}
